/**
 * @file 全部礼包
 */
@import '../../assets/styles/core/base';
@import '../../assets/styles/core/setting';
@import '../../assets/styles/core/reset';

::-webkit-input-placeholder {
  color: #C5CAD4;
}
:-moz-placeholder {/* Firefox 18- */
  color: #C5CAD4;
}
::-moz-placeholder{/* Firefox 19+ */
  color: #C5CAD4;
}
:-ms-input-placeholder {
  color: #C5CAD4;
}

.reservation_detail_container {
  width: 100%;
  padding-bottom: constant(safe-area-inset-bottom);
  padding-bottom: env(safe-area-inset-bottom);
  box-sizing: border-box;
  .blank {
    height: pr2(82);
  }
  .card_wrapper {
    margin: pr2(12) pr2(20) 0;
    padding: 0 pr2(20);
    box-sizing: border-box;
    background: #fff;
    box-shadow: 0 pr2(4) pr2(20) 0 rgba(17, 30, 54, .06);
    border-radius: pr2(4);
    &:last-child {
      margin-bottom: 0;
    }
  }
  .car_null_card {
    height: pr2(100);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    .btn_add_car {
      margin-bottom: pr2(10);
      width: pr2(140);
      height: pr2(32);
      background-color: #0055FF;
      border-radius: pr2(4);
      font-size: pr2(12);
      color: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
      .icon_plus {
        margin-right: pr2(7);
        display: block;
        width: pr2(8);
        height: pr2(8);
        background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABsAAAAYCAYAAAALQIb7AAAAdUlEQVRIS+1UwQ2AMBCCrVxEB3EQB9FF3OpME9M09yiN9sl9D0JCAGLgImIFcCboRvIaoFcIR8AWUy7ZRqexmxG+HVJBWgDsCXQAuBWxLX4RC0X48ydZE2+xz05mG0uH1M0JiFIpf6++csmr79Xvr75K0MyePZ6hdhkTQBh3AAAAAElFTkSuQmCC') center center no-repeat;
        background-size: 100%;
      }
    }
    .tip {
      font-size: pr2(12);
      color: #666D7F;
      line-height: 1;
    }
  }

  .cell {
    padding: pr2(13) 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #111E36;
    line-height: pr2(24);
    .hd {
      font-size: pr2(16);
      font-family: PingFangSC-Medium,PingFang SC;
      font-weight: 500;
      min-width: pr2(66);
      margin-right: pr2(8);
      &.service_name_title {
        font-size: pr2(18);
      }
      &.service_name {
        font-family: PingFangSC-Regular,PingFang SC;
        font-weight: 400;
      }
    }
    .bd {
      flex: 1;
      font-size: pr2(16);
      text-align: right;
      &.service_price {
        font-size: pr2(14);
        color: #666D7F;
        span {
          font-size: pr2(18);
          color: #FF6600;
          font-family: PingFangSC-Semibold,PingFang SC;
          font-weight: 600;
        }
      }
      input {
        width: 100%;
        appearance: none;
        -moz-appearance: none;
        -webkit-appearance: none;
        border: 0;
        padding: 0;
        outline: none;
        color: #111E36;
        text-align: right;
      }
    }
    .ft {
      margin-left: pr2(4);
      width: pr2(12);
      height: pr2(12);
      background: url('data:image/png;base64,') center center no-repeat;
      background-size: 100%;
    }
    &.dealer_name {
      align-items: flex-start;
      .bd {
        max-height: pr2(48);
        @include ellipsisLn(2);
        width: pr2(221);
      }
    }
    &.dealer_address {
      align-items: flex-start;
      .bd {
        max-height: pr2(72);
        @include ellipsisLn(3);
        width: pr2(221);
        .icon_address {
          display: inline-block;
          vertical-align: middle;
          margin-right: pr2(5);
          margin-top: pr2(-2);
          width: pr2(16);
          height: pr2(16);
          background: url('data:image/png;base64,') center center no-repeat;
          background-size: 100%;
        }
      }
    }
    &.cell_service {
      padding-top: 0;
      padding-bottom: pr2(8);
      line-height: pr2(20);
      &:last-child {
        padding-bottom: pr2(16);
      }
    }
  }

  .time_bucket_box {
    padding: 0 pr2(10) pr2(8);
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    .time_bucket_item {
      margin-bottom: pr2(8);
      width: pr2(108);
      height: pr2(40);
      font-size: pr2(14);
      color: #0055FF;
      line-height: pr2(38);
      border-radius: pr2(4);
      border: 1px solid rgba(0, 85, 255, .2);
      text-align: center;
      box-sizing: border-box;
      &.active {
        background: #0055FF;
        color: #fff;
        border: 0;
        line-height: pr2(40);
      }
    }
  }

  .reservation_btn_box {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    padding-bottom: constant(safe-area-inset-bottom);
    padding-bottom: env(safe-area-inset-bottom);
    box-sizing: border-box;
    background: #fff;
    .reservation_btn {
      margin: pr2(8) pr2(20);
      height: pr2(50);
      background: rgba(241,243,246,1);
      color:rgba(197,202,212,1);
      text-align: center;
      line-height: pr2(50);
      font-size: pr2(16);
      border-radius: pr2(4);
      &.can_submit {
        background: #0055FF;
        color: #fff;
      }
    }
  }
}
